import React from "react";

function List({ item }) {
    return (
        <div className="item3">
            <img src={item.src} alt="" />
            <h4>{item.title}</h4>
            <p>
                {item.price}
                <span>{item.txt}</span>
            </p>
        </div>
    );
}

