import React from "react";
import styles from "./project.module.css";

function ComProItem({title02, date02, src02}){
    return(
        <div className={styles.item02}>
            <div className={styles.img02}>
                <img src={src02} width="100%" alt="" />
            </div>
            <h5>{title02}</h5>
            <p>{date02}</p>
        </div>
    )
}

export default ComProItem 