import React from "react";
import styles from "./event01.module.css";

function Event01List({ date, info, src }) {
    return (
        <div className={styles.item}>
            <div className={styles.img}>
                <img src={src} width="100%" alt="" />
            </div>
            <div className={styles.infobox}>
                <span>{info}</span>
            </div>
            <p>{date}</p>
        </div>
    );
}

export default Event01List;
