import React from "react";
import styles from "./event01.module.css";

function Event02List({ date, info, src }) {
    return (
        <div className={styles.item02}>
            <div className={styles.img02}>
                <img src={src} width="100%" alt="" />
            </div>
            <div className={styles.infobox02}>
                <span>{info}</span>
            </div>
            <p>{date}</p>
        </div>
    );
}

export default Event02List;
