import React from "react";

function Wa01BList({src, title, txt}){
    return(
        <div className='wa01_b_item'>
            <div className='styles.img'>
                <img src={src} width='100%'  alt="" />
            </div>
            <h5>{title}</h5>
            <p>{txt}</p>
        </div>
    )
}

export default Wa01BList