import React from "react";
import styles from "./product.module.css";
import ProductList from "./productlist";

function Product() {
    return (
        <div>
            <div className={styles.mainimg}>
                <h3>냉장제품</h3>
                <div>
                    <p>총 24개의 상품이 있습니다.</p>
                    <button>상세검색</button>
                </div>
            </div>
            <ProductList />
        </div>
    );
}

export default Product;
