import React from "react";

function HaList({src, title, price}){
    return(
        <div className='halist_item'>
            <div className="img">
                <img src={src} width='100%' alt="" />
            </div>
            <h5>{title}</h5>
            <p>{price}</p>
        </div>
    )
}

export default HaList