import React from "react";
import styles from "./brand.module.css";

function BrandList({ title, info, src }) {
    return (
        <div className={styles.item}>
            <div className={styles.img}>
                <img src={src} width="100%" alt="" />
            </div>
            <span>{info}</span>
            <p>{title}</p>
        </div>
    );
}

export default BrandList;
