import React from "react";
import styles from "./delivery.module.css";

function DeliveryList03({ price, info, src }) {
    return (
        <div className={styles.item03}>
            <div className={styles.img03}>
                <img src={src} alt="" />
            </div>
            <span>{info}</span>
            <p>{price}</p>
        </div>
    );
}

export default DeliveryList03;
