import React from "react";
import styles from "./delivery.module.css";

function DeliveryList02({ price, info, src }) {
    return (
        <div className={styles.item02}>
            <div className={styles.img02}>
                <img src={src} width="100%" alt="" />
            </div>
            <span>{info}</span>
            <p>{price}</p>
        </div>
    );
}

export default DeliveryList02;
