import React from "react";
import styles from "./project.module.css";

function ProjectItem({ title, date, src }) {
    return (
        <div className={styles.item}>
            <div className={styles.img}>
                <img src={src} width="100%" alt="" />
            </div>
            <h5>{title}</h5>
            <p>{date}</p>
        </div>
    );
}

export default ProjectItem;
