import React from "react";
import styles from "./member.module.css";

function Member01List({ src, title }) {
    return (
        <div className={styles.item}>
            <div className={styles.img}>
                <img src={src} width="100%" alt="" />
            </div>
            <h5>{title}</h5>
        </div>
    );
}

export default Member01List;
