import React from "react";
import styles from "./product.module.css";

function List({ item }) {
    return (
        <div className={styles.item}>
            <img src={item.src} alt="" />
            <h4>{item.title}</h4>
            <p>
                {item.price}
                <span>{item.txt}</span>
            </p>
        </div>
    );
}

